import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-members',
  templateUrl: './members.component.html',
  styleUrls: ['./members.component.css','./skel.css','./style.css','./style-xlarge.css','./font-awesome.min.css']
})
export class MembersComponent implements OnInit {

  users={};
  userService: UserService;

  constructor(private user: UserService, private route: ActivatedRoute, private router: Router) { 
    this.userService = user;
  }

  ngOnInit() {
    this.getUser();
  }

  getUser(){
    var username = sessionStorage.getItem('member');
    this.user.queryUser({
      username: username
    }).subscribe(
      result => {
        console.log(result[0]);
        this.users = result[0]
        console.log("starting => "+result[0].LinkedIn);
      },
      error => {
        console.log(error);
        
      }
    )
    localStorage.removeItem('member');
    console.log(username);
  }

  logout(){
    localStorage.clear();
    this.router.navigate(['/']);
  }
}



