import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../../services/user.service';

@Component({
  selector: 'app-priavate-people',
  templateUrl: './priavate-people.component.html',
  styleUrls: ['./priavate-people.component.css','./skel.css','./style.css','./style-xlarge.css','./font-awesome.min.css']
})
export class PriavatePeopleComponent implements OnInit {

  userService: UserService;
  students: any[];

  constructor(private router: Router, private student: UserService) {
    this.userService = student;
  }

  ngOnInit() {
    this.getMembers();
  }

  getMembers(){
    this.student.getMembers().subscribe(
      students => {
        this.students = students;
        
      },
      error => {
        console.log(error);
      }
    );
  }

  logout(){
    localStorage.clear();
    this.router.navigate(['/']);
  }

  update(){
    var val = document.forms['userForm']['viewbtn'].value;
    sessionStorage.setItem('member',val);
    console.log("value "+ val);
    this.router.navigate(["/member"]);
  }

}



